import axios from "axios";
import React, { useState, useEffect } from "react";

const proxyAPI2 = "https://cors-anywhere.herokuapp.com/";
const proxyAPI = "http://thingproxy.freeboard.io/";
const proxyAPI3 = "http://www.whateverorigin.org/get?url=";

function Strategies() {
  const [searchTerm, setSearchTerm] = React.useState("");
  const [searchResults, setSearchResults] = React.useState([]);
  const [watchList, setWatchList] = React.useState([]);
  const [strategies, setStrategies] = React.useState([]);

  const handleChange = (event) => {
    setSearchTerm(event.target.value);
  };

  const handleKeypress = (e) => {
    //it triggers by pressing the enter key
    if (e.keyCode === 13) {
      searchStock();
    }
  };

  const searchStock = () => {
    if (searchTerm.length == 0) {
      alert("Please enter name of stock");
      return;
    }

    let searchApi =
      "https://query2.finance.yahoo.com/v1/finance/search?q=" +
      searchTerm +
      "&quotesCount=6&newsCount=0&enableFuzzyQuery=false&quotesQueryId=tss_match_phrase_query&multiQuoteQueryId=multi_quote_single_token_query&newsQueryId=news_ss_symbols&enableCb=false&enableNavLinks=false&vespaNewsTimeoutMs=600";
    axios
      .request({
        method: "get",
        url: `https://api.allorigins.win/get?url=${encodeURIComponent(
          searchApi
        )}`,
      })
      .then((response) => {
        console.log(response);
        setSearchResults(JSON.parse(response.data.contents).quotes);
      });
  };

  // const addStockToList = (item) => {
  //   console.log(watchList.some(i=>i.symbol === item.symbol));
  //   if(!watchList.some(i=>i.symbol === item.symbol))
  //     setWatchList([item,...watchList]);
  // };
    
  const addStockToList = (item) => {
    axios
    .request({
      method: "post",
      url: "https://us-central1-stockwatch-193209.cloudfunctions.net/stocks",
      data: item,
    })
    .then((response) => {
      console.log(response.data);
      getAllStocks();
    });    
  };

  // const removeStockFromList = (item) => {
  //   var filtered = watchList.filter(el => { return el.symbol !== item.symbol; });
  //     setWatchList([...filtered]);
  // };

  const getAllStocks = () => {
    axios
    .request({
      method: "get",
      url: "https://us-central1-stockwatch-193209.cloudfunctions.net/stocks",
    })
    .then((response) => {
      console.log(response.data);
      setWatchList(response.data);
    });
  }

  const removeStock = (stockSymbol) => {
    axios
    .request({
      method: "post",
      url: "https://us-central1-stockwatch-193209.cloudfunctions.net/stocks/delete/"+stockSymbol,
    })
    .then((response) => {
      console.log(response.data);
      getAllStocks();
    });
  }

  const getAllStrategies = () => {
    axios
    .request({
      method: "get",
      url: "https://us-central1-stockwatch-193209.cloudfunctions.net/strategy",
    })
    .then((response) => {
      console.log(response.data);
      setStrategies(response.data);
    });
  }

  useEffect(() => {
    getAllStocks();
    getAllStrategies();
  }, []);

  return (
    <div className="App">
      <input
        type="text"
        placeholder="Search"
        value={searchTerm}
        onChange={handleChange}
        onKeyDown={handleKeypress}
      />
      <button
        onClick={() => {
          searchStock();
        }}
      >
        {" "}
        Search{" "}
      </button>
      {/* {JSON.stringify()} */}
      <table border="1px solid black;">
        <th>SearchList</th>
        <tr>
          <th>{"Exchange"}</th>
          <th>{"Name"}</th> <th>{"Price"}</th>
        </tr>
        {searchResults.map((item) => {
          return (
            <tr>
              <td>{item.exchDisp}</td> <td>{item.shortname}</td>{" "}
              <td>
                {item.score}
                {"  "}
              </td>
              <td>
                {
                  <button
                    onClick={() => {
                      addStockToList(item);
                    }}
                  >
                    Add
                  </button>
                }
              </td>
            </tr>
          );
        })}
      </table>

      <br/>
      <div style={{display:"flex", flexDirection:"row"}}>
      &nbsp;&nbsp;
      <table border="1px solid black;">
        <tr><th>Strategy</th></tr>
        {strategies.length > 0 ? (
          <tr>
            <th>{"Exchange"}</th>
            <th>{"Name"}</th> 
            <th>{"Price"}</th>
          </tr>
        ) : null}
        {strategies.map((item) => {
          return (
            <tr>
              <td>{item.name}</td> <td>{item.name}</td>{" "}
              <td>
                {item.score}
                {"  "}
              </td>
              <td>{<button onClick={()=>{}}>Remove</button>}</td>
              {/* <td>{JSON.stringify(item)}</td> */}
            </tr>
          );
        })}
      </table>
      </div>
    </div>
  );
}

export default Strategies;
