import axios from "axios";
import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux'
import { setstockpricesR, setsearchtermR, setsearchlistR, setwatchlistR } from './watchlistslice';

const proxyAPI2 = "https://cors-anywhere.herokuapp.com/";
const proxyAPI = "http://thingproxy.freeboard.io/";
const proxyAPI3 = "http://www.whateverorigin.org/get?url=";

function WatchList() {
  const dispatch = useDispatch();

  const stockpricesR = useSelector((state) => state.watchlist.stockprices);  
  const searchlistR = useSelector((state) => state.watchlist.searchlist);
  const searchTermR = useSelector((state) => state.watchlist.searchterm);
  const watchlistR = useSelector((state) => state.watchlist.watchlist);

  // useEffect(() => {
  //   const delayDebounceFn = setTimeout(() => {
  //     console.log(searchTermR)
  //     // Send Axios request here
  //     // searchStock();
  //   }, 3000)

  //   return () => clearTimeout(delayDebounceFn)
  // },[searchTermR]);

  const handleChange = (event) => {
    dispatch(setsearchtermR(event.target.value));
    if (searchTermR.length == 0) {
        return;
    }
    // searchStock();
  };

  const handleKeypress = (e) => {
    //it triggers by pressing the enter key
    if (e.keyCode === 13) {
        if (searchTermR.length == 0) {
            alert("Please enter name of stock");
            return;
          }
      searchStock();
    }
  };

  const searchStock = () => {
    let searchApi =
      "https://query2.finance.yahoo.com/v1/finance/search?q=" +
      searchTermR +
      "&quotesCount=6&newsCount=0&enableFuzzyQuery=false&quotesQueryId=tss_match_phrase_query&multiQuoteQueryId=multi_quote_single_token_query&newsQueryId=news_ss_symbols&enableCb=false&enableNavLinks=false&vespaNewsTimeoutMs=600";

      let body = {
        "url": searchApi
    };
    axios.post(`https://us-central1-stockwatch-193209.cloudfunctions.net/getstocks`, body)
      .then((response) => {
        // console.log(response);
        // let resp = JSON.parse(response);
        dispatch(setsearchlistR(response.data.quotes));
      });
  };
    
  const addStockToList = (item) => {
    axios
    .request({
      method: "post",
      url: "https://us-central1-stockwatch-193209.cloudfunctions.net/stocks",
      data: item,
    })
    .then((response) => {
      console.log(response.data);
      getAllStocks();
    });    
  };

  const getAllStocks = () => {
    axios
    .request({
      method: "get",
      url: "https://us-central1-stockwatch-193209.cloudfunctions.net/stocks",
    })
    .then((response) => {
      console.log(response.data);
      dispatch(setwatchlistR(response.data));
      getStocksDetails(response.data);
    });
  }

  const removeStock = (stockSymbol) => {
    axios
    .request({
      method: "post",
      url: "https://us-central1-stockwatch-193209.cloudfunctions.net/stocks/delete/"+stockSymbol,
    })
    .then((response) => {
      console.log(response.data);
      getAllStocks();
    });
  }

  const getStocksDetails = (stocks) => {
    const stockSymbList = stocks.reduce(function (result, item) {
      if(result == "")
      return item.symbol;
      return result + "," + item.symbol;
    }, "");

    let searchApi = "https://query1.finance.yahoo.com/v7/finance/spark?symbols=" + stockSymbList + "&range=1d&interval=5m&indicators=close&includeTimestamps=false&includePrePost=false&corsDomain=in.finance.yahoo.com&.tsrc=finance";

    let body = {
      "url": searchApi
    };
    axios.post(`https://us-central1-stockwatch-193209.cloudfunctions.net/getstocks`, body)
    .then((response) => {
      console.log(response);
      console.log(response.data.spark.result[0].response[0].meta.regularMarketPrice);
      // let resp = JSON.parse(response);
      // dispatch(setstockpricesR({"INFY.NS":"10", "BABA":"20"}));
      let priceMap = createStockPriceMap(response.data.spark.result);
      dispatch(setstockpricesR(priceMap));
    });
  }

  const createStockPriceMap = (stocksData) => {
    return stocksData.reduce((result, item) => {
      return { [item.symbol]: item.response[0].meta.regularMarketPrice, ...result };
    }, {});
  }

  useEffect(() => {
    getAllStocks();
    // getAllStrategies();
  }, []);

  return (
    <div className="App">
      <input
        type="text"
        placeholder="Search"
        value={searchTermR}
        onChange={handleChange}
        onKeyDown={handleKeypress}
      />
      <button
        onClick={() => {
          searchStock();
        }}
      >
        {" "}
        Search{" "}
      </button>
      {/* {JSON.stringify(watchlistR)} */}
      <table border="1px solid black;">
        <th>Search List</th>
        <tr>
          <th>{"Exchange"}</th>
          <th>{"Name"}</th>
        </tr>
        {searchlistR.map((item) => {
          return (
            <tr>
              <td>{item.exchDisp}</td> <td>{item.shortname}</td>{" "}
              <td>
                {
                  <button
                    onClick={() => {
                      addStockToList(item);
                    }}
                  >
                    Add
                  </button>
                }
              </td>
            </tr>
          );
        })}
      </table>

      <br/>
      <div style={{display:"flex", flexDirection:"row"}}>
      <table border="1px solid black;">
        <tr><th>WatchList</th></tr>
        {watchlistR.length > 0 ? (
          <tr>
            <th>{"Exchange"}</th>
            <th>{"Name"}</th> 
            <th>{"Price"}</th>
          </tr>
        ) : null}
        {/* {console.log("WatchList: ", JSON.stringify(watchlistR))} */}
        {watchlistR.map((item) => {
          return (
            <tr>
              <td>{item.exchDisp}</td> <td>{item.shortname}</td>{" "}
              <td>
                {stockpricesR[item.symbol]}
                {"  "}
              </td>
              <td>{<button onClick={()=>removeStock(item.symbol)}>Remove</button>}</td>
              {/* <td>{JSON.stringify(item)}</td> */}
            </tr>
          );
        })}
      </table>
      </div>
    </div>
  );
}

export default WatchList;
