import { createSlice } from '@reduxjs/toolkit'

const initialState = {
  stockprices: {},
  searchterm: "",
  searchlist: [],
  watchlist: [],
}

export const watchlistslice = createSlice({
  name: 'watchlist',
  initialState,
  reducers: {
    setstockpricesR: (state, action) => {
      state.stockprices = action.payload;
    },
    setsearchtermR: (state, action) => {
      state.searchterm = action.payload;
    },
    setsearchlistR: (state, action) => {
      state.searchlist = action.payload
    },
    setwatchlistR: (state, action) => {
    //   console.log("Action Called: ", action.payload);
      state.watchlist = action.payload
    },
  },
})

// Action creators are generated for each case reducer function
export const { setstockpricesR, setsearchtermR, setsearchlistR, setwatchlistR } = watchlistslice.actions

export default watchlistslice.reducer